@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap);
body {
    margin: 0;
    padding: 0;
    font-family: "Poppins", sans-serif;
    box-sizing: border-box;
}

:root {
    --primary-color: #fd2d47;
    --background-color: rgb(17, 22, 42);
    --background-accent-color: rgb(44, 56, 86);
    --background-secondary-color: rgb(60, 62, 77);
    --text-color: rgb(255, 255, 255);
    --secondary-text-color: rgb(122, 124, 133);
    --yellow: rgb(240, 239, 11);
    --yellow-accent: rgb(161, 161, 11);
    --red-pale: rgb(239, 106, 107);
    --button-follow: rgb(255, 255, 255);
    --hint-color: rgba(255, 255, 255, 0.15);
    --main-padding-sides: 40px;
    --main-padding-sides-mobile: 18px;
}

[data-theme="light"] {
    --primary-color: rgb(74, 178, 169);
    --background-color: rgb(255, 255, 255);
    --background-accent-color: rgb(243, 243, 245);
    --background-secondary-color: rgb(246, 246, 246);
    --text-color: rgb(13, 13, 13);
    --secondary-text-color: rgb(51, 51, 51);
    --yellow: rgb(74, 178, 169);
    --yellow-accent: rgb(54, 141, 134);
    --button-follow: rgba(0, 0, 0, 0.2);
    --hint-color: rgba(0, 0, 0, 0.15);
}

a {
    color: transparent;
}

#root {
    height: 100vh;
}
.container {
    grid-area: sidebar;
    padding: 50px 32px;
    background: var(--background-accent-color);
    min-width: 150px;
    max-width: 200px;
}

.container .item {
    display: block;
    color: var(--text-color);
    text-decoration: none;
    margin: 20px 0;
}

.container .item:hover {
    cursor: pointer;
    color: rgba(255, 255, 255, 0.6);
}

.container .item span {
    margin-left: 12px;
}

.container .sidebar-library {
    margin-top: 100px;
}

.container .sidebar-library h4 {
    font-weight: 400;
    margin-left: 12px;
    color: var(--secondary-text-color);
}

.container .sidebar-mobile {
    display: none;
}

.icon {
    display: none;
}

.active span,
.active .icon {
    color: var(--yellow);
}

@media (min-width: 700px) {
    .container {
        min-height: 100vh;
        position: fixed;
        top: 0;
        left: 0;
    }
}

@media (max-width: 700px) {
    .icon {
        display: block;
    }
    .container {
        display: -webkit-flex;
        display: flex;
        height: 100%;
        min-width: 100vw;
        padding: 0;
        position: -webkit-sticky;
        position: sticky;
        bottom: 0;
        z-index: 9999;
    }
    .container .sidebar-library,
    .sidebar-topics {
        display: none;
    }
    .container .sidebar-mobile {
        display: -webkit-flex;
        display: flex;
        width: 100%;
    }
    .container .sidebar-mobile .item {
        -webkit-flex: 1 1;
                flex: 1 1;
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-justify-content: center;
                justify-content: center;
        -webkit-align-items: center;
                align-items: center;
        margin: 0;
    }
    .container .item span {
        margin: 0;
    }
}
